import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanStockPerKategoriLokasi = (data) => {
  // initialize jsPDF
  const doc = new jsPDF();
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  let tableColumn = [];

  // let data = [
  //   {
  //     kode_supplier: "SP001",
  //     nama_supplier: "SUPPLIER",
  //     detail: [
  //       {
  //         tanggal_invoice: "2021-03-01",
  //         no_bon: "CB44C4-F832-031A-3026-EB855A3D777F",
  //         detail_barang: [
  //           {
  //             no_urut: 1,
  //             kode_jenis: "990PER107",
  //             keterangan: "990 PER 107",
  //             kadar_cetak: 100,
  //             kadar_beli: 99,
  //             kode_barcode: "-",
  //             qty: 2,
  //             plastik: 2,
  //             kulit: 0,
  //             pkg: 5,
  //             gross: 10,
  //             bruto: 5,
  //             netto: 4.95,
  //             ongkos_rp: 100000,
  //           },
  //           {
  //             no_urut: 2,
  //             kode_jenis: "GLKEPRAS78",
  //             keterangan: "GLKEPRAS 78",
  //             kadar_cetak: 78,
  //             kadar_beli: 100,
  //             kode_barcode: "-",
  //             qty: 5,
  //             plastik: 5,
  //             kulit: 0,
  //             pkg: 5,
  //             gross: 60,
  //             bruto: 55,
  //             netto: 55,
  //             ongkos_rp: 100000,
  //           },
  //         ],
  //         total_bruto: 60,
  //         total_netto: 59.95,
  //         total_netto_ext: 59.95,
  //         total_rp: 200000,
  //       },
  //     ],
  //   },
  // ];
  doc.setFontSize(15);
  doc.text("LAPORAN STOCK PER LOKASI", 14, 15);
  doc.setFontSize(10);
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    tableColumn = [
      [
        {
          content: `KODE LOKASI : ${item.kode_lokasi}`,
          colSpan: 4,
        },
      ],
      [
        {
          content: `KODE KATEGORI`,
        },
        {
          content: `KODE JENIS`,
        },
        {
          content: `TOTAL BRUTO`,
        },
        {
          content: `TOTAL NETTO`,
        },
      ],
    ];
    item.detail.forEach((hasil) => {
      let rows = [
        hasil.kode_kategori,
        hasil.kode_jenis,
        formatGram(hasil.total_bruto),
        formatGram(hasil.total_netto),
      ];
      tableRows.push(rows);
    });
  });
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: 30,
    theme: "plain",
    rowPageBreak: "avoid",

    margin: { top: 20 },
    bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
    headStyles: {
      lineWidth: 0.02,
      lineColor: "#000",
      fillColor: [212, 212, 211],
    },
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN STOCK PER LOKASI",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN STOCK PER LOKASI</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanStockPerKategoriLokasi;
