import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getkategoriList } from "../../actions/kategoriAction";
import { getListBarang } from "../../actions/kodeBarangAction";
import { getLokasiPindah } from "../../actions/supplierAction";
import {
  ReanderField,
  ReanderSelect,
} from "../../GlobalComponent/notification";

const renderField = ({
  input,
  label,
  type,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input {...input} type={type} className="form-control" />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormStok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      kode_perhiasan: "",
      tipeOmzet: "PERTAHUN",
      listSupplier: [],
      filter: true,
      listKategori: [
        {
          nama: "AMBIL BARANG",
        },
        {
          nama: "KEMBALI STOK",
        },
        {
          nama: "KOREKSI STOK",
        },
        {
          nama: "MUTASI BAHAN",
        },
        {
          nama: "PENGAMBILAN SALES",
        },
        {
          nama: "PINDAH",
        },
        {
          nama: "PINDAH GUDANG REPARASI",
        },
        {
          nama: "RETURN SALES",
        },
        {
          nama: "SALAH INPUT",
        },
        {
          nama: "SALAH PINDAH",
        },
        {
          nama: "STOK AWAL",
        },
        {
          nama: "TAMBAH BARANG",
        },
      ],
    };
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  componentDidMount() {
    this.props.dispatch(getkategoriList());
    this.props.dispatch(getLokasiPindah());
    this.props.dispatch(getListBarang());
  }
  render() {
    switch (this.props.from) {
      case "saldoAwal":
        return (
          <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Lokasi</label>
              <Field name="lokasi" className="form-control" component="select">
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
              </Field>
            </div>
            <div className="col-lg-4">
              <Field
                name="tglFrom"
                label="Mulai Tanggal"
                className="form-control"
                component={renderField}
                type="date"
              />
            </div>
            <div className="col-lg-4">
              <Field
                name="tglEnd"
                label="Sampai Dengan"
                className="form-control"
                component={renderField}
                type="date"
              />
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">Print</button>
              </div>
            </div>
          </div>
        );

      case "kriteria":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <label htmlFor="">Kriteria</label>
                <Field
                  name="kriteria"
                  className="form-control"
                  component="select"
                >
                  <option value="DEFAULT" key="DEFAULT">
                    SILAHKAN PILIH
                  </option>
                  {this.state.listKategori.map((list) => (
                    <option value={list.nama}>{list.nama}</option>
                  ))}
                </Field>
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );

      case "perpindahanBarang":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <label htmlFor="">Jenis</label>
                <Field name="jenis" className="form-control" component="select">
                  <option value="DEFAULT" key="DEFAULT">
                    SILAHKAN PILIH
                  </option>
                </Field>
              </div>
              <div className="col-lg-4">
                <label htmlFor="">Dari Lokasi</label>
                <Field
                  name="kodeLokasiFrom"
                  className="form-control"
                  component="select"
                >
                  <option value="DEFAULT" key="DEFAULT">
                    SILAHKAN PILIH
                  </option>
                  {this.state.listSupplier.map((list) => (
                    <option value={list.kodeSupplier}>
                      {list.namaSupplier}
                    </option>
                  ))}
                </Field>
              </div>
              <div className="col-lg-4">
                <label htmlFor="">Ke Lokasi</label>
                <Field
                  name="kodeLokasiTo"
                  className="form-control"
                  component="select"
                >
                  <option value="DEFAULT" key="DEFAULT">
                    SILAHKAN PILIH
                  </option>
                  {this.state.listSupplier.map((list) => (
                    <option value={list.kodeSupplier}>
                      {list.namaSupplier}
                    </option>
                  ))}
                </Field>
              </div>
              <div className="col-lg-4 mt-3">
                <div className="row">
                  <div className="col-lg-6">
                    <label htmlFor="employed">TAMBAH BARANG</label>
                    <div>
                      <Field
                        name="tambahBarang"
                        id="employed"
                        component="input"
                        type="checkbox"
                        value="TAMBAH BARANG"
                      />
                    </div>
                  </div>
                  <div className="col-lg-6">
                    <label htmlFor="employed">AMBIL BARANG</label>
                    <div>
                      <Field
                        name="ambilBarang"
                        id="employed"
                        component="input"
                        type="checkbox"
                        value="AMBIL BARANG"
                      />
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-lg-8"></div>
              <div className="col-lg-4 mt-3">
                <Field
                  name="tglFrom"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4 mt-3">
                <Field
                  name="tglTo"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );

      case "kartuStok":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_lokasi"
                  component={ReanderSelect}
                  options={this.props.listlokasi.map((list) => {
                    let data = {
                      value: list.kode_lokasi,
                      name: `${list.kode_lokasi} - ${list.nama_lokasi}`,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Lokasi"
                  placeholder="Masukan Kode Lokasi"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="kode_jenis"
                  component={ReanderSelect}
                  options={this.props.kodeBarang.map((list) => {
                    let data = {
                      value: list.kode_jenis,
                      name: `${list.kode_jenis} - ${list.nama_jenis}`,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Jenis"
                  placeholder="Masukan Kode Jenis"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_awal"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Awal"
                  placeholder="Masukan Tanggal Awal"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_akhir"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Akhir"
                  placeholder="Masukan Tanggal Akhir"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );

      case "stokPerKategori":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="group_by"
                  component={ReanderSelect}
                  options={[
                    {
                      value: "LOKASI",
                      name: "LOKASI",
                    },
                    {
                      value: "KATEGORI",
                      name: "KATEGORI",
                    },
                  ]}
                  type="text"
                  label="Group By"
                  placeholder="Masukan Group By"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="filter_by"
                  component={ReanderSelect}
                  options={[
                    {
                      value: "LOKASI",
                      name: "LOKASI",
                    },
                    {
                      value: "KATEGORI",
                      name: "KATEGORI",
                    },
                  ]}
                  type="text"
                  label="Filter By"
                  placeholder="Masukan Filter By"
                  onChange={(data) => {
                    data === "KATEGORI"
                      ? this.setState({
                          filter: false,
                        })
                      : this.setState({
                          filter: true,
                        });
                  }}
                />
              </div>
              <div
                className={this.state.filter ? "col-lg-3 d-none" : "col-lg-3 "}
              >
                <Field
                  name="kode_kategori"
                  component={ReanderSelect}
                  options={this.props.listkategori.map((list) => {
                    let data = {
                      value: list.kode_kategori,
                      name: `${list.nama_kategori} - ${list.kode_kategori}`,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Kategori"
                  placeholder="Masukan Kode Kategori"
                />
              </div>
              <div
                className={this.state.filter ? "col-lg-3 " : "col-lg-3 d-none"}
              >
                <Field
                  name="kode_lokasi"
                  component={ReanderSelect}
                  options={this.props.listlokasi.map((list) => {
                    let data = {
                      value: list.kode_lokasi,
                      name: list.nama_lokasi,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Lokasi"
                  placeholder="Masukan Kode Lokasi"
                />
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary">Print</button>
                </div>
              </div>
            </div>
          </form>
        );

      case "soSales":
        return (
          <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Lokasi</label>
              <Field name="lokasi" className="form-control" component="select">
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
              </Field>
            </div>
            <div className="col-lg-4">
              <Field
                name="tanggal"
                label="Tanggal"
                className="form-control"
                component={renderField}
                type="date"
              />
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">Print</button>
              </div>
            </div>
          </div>
        );

      case "jenisJK":
        return (
          <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Jenis</label>
              <Field name="jenis" className="form-control" component="select">
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
              </Field>
            </div>
            <div className="col-lg-4">
              <Field
                name="tglFrom"
                label="Mulai Tanggal"
                className="form-control"
                component={renderField}
                type="date"
              />
            </div>
            <div className="col-lg-4">
              <Field
                name="tglTo"
                label="Sampai Dengan"
                className="form-control"
                component={renderField}
                type="date"
              />
            </div>
            <div className="col-lg-4">
              <div className="form-group">
                <label>Type Laporan</label>
                <Field
                  name="typeLap"
                  className="form-control"
                  component="select"
                >
                  <option value="DETAIL" key="DETAIL">
                    DETAIL
                  </option>
                  <option value="REKAPITULASI" key="REKAPITULASI">
                    REKAPITULASI
                  </option>
                </Field>
              </div>
            </div>
            <div className="col-lg-4">
              <div className="form-group">
                <label htmlFor="">Jenis Laporan</label>
                <div className="form-control-transparant">
                  <label className="mr-3">
                    <Field
                      name="jenisLaporan"
                      component="input"
                      type="radio"
                      value="bruto"
                      className="mr-2"
                    />
                    Bruto
                  </label>
                  <label>
                    <Field
                      name="jenisLaporan"
                      component="input"
                      type="radio"
                      value="netto"
                      className="mr-2"
                    />
                    Netto
                  </label>
                </div>
              </div>
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">Print</button>
              </div>
            </div>
          </div>
        );

      case "kirimMasak":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <div className="form-group">
                  <label htmlFor="">Jenis Laporan</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="jenisLaporanKM"
                        component="input"
                        type="radio"
                        value="KIRIM MASAK"
                        className="mr-2"
                      />
                      Kirim Masak
                    </label>
                    <label>
                      <Field
                        name="jenisLaporanKM"
                        component="input"
                        type="radio"
                        value="KIRIM PRODUKSI"
                        className="mr-2"
                      />
                      Kirim Produksi
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );

      case "terimaMasak":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-4">
                <Field
                  name="tglFrom"
                  label="Mulai Tanggal"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <Field
                  name="tglTo"
                  label="Sampai Dengan"
                  className="form-control"
                  component={renderField}
                  type="date"
                />
              </div>
              <div className="col-lg-4">
                <div className="form-group">
                  <label htmlFor="">Jenis Laporan</label>
                  <div className="form-control-transparant">
                    <label className="mr-3">
                      <Field
                        name="jenisLaporanTM"
                        component="input"
                        type="radio"
                        value="TERIMA MASAK"
                        className="mr-2"
                      />
                      Terima Masak
                    </label>
                    <label>
                      <Field
                        name="jenisLaporanTM"
                        component="input"
                        type="radio"
                        value="TERIMA PRODUKSI"
                        className="mr-2"
                      />
                      Terima Produksi
                    </label>
                  </div>
                </div>
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button className="btn btn-primary" type="submit">
                    Print
                  </button>
                </div>
              </div>
            </div>
          </form>
        );
      case "SummaryStock":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_lokasi"
                  component={ReanderSelect}
                  options={this.props.listlokasi.map((list) => {
                    let data = {
                      value: list.kode_lokasi,
                      name: `${list.kode_lokasi} - ${list.nama_lokasi}`,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Lokasi"
                  placeholder="Masukan Kode Lokasi"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_awal"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Awal"
                  placeholder="Masukan Tanggal Awal"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_akhir"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Akhir"
                  placeholder="Masukan Tanggal Akhir"
                />
              </div>
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">Print</button>
              </div>
            </div>
          </form>
        );
      case "mutasi":
        return (
          <form onSubmit={this.props.handleSubmit}>
            <div className="row">
              <div className="col-lg-3">
                <Field
                  name="kode_lokasi"
                  component={ReanderSelect}
                  options={this.props.listlokasi.map((list) => {
                    let data = {
                      value: list.kode_lokasi,
                      name: `${list.kode_lokasi} - ${list.nama_lokasi}`,
                    };
                    return data;
                  })}
                  type="text"
                  label="Kode Lokasi"
                  placeholder="Masukan Kode Lokasi"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_awal"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Awal"
                  placeholder="Masukan Tanggal Awal"
                />
              </div>
              <div className="col-lg-3">
                <Field
                  name="tanggal_akhir"
                  component={ReanderField}
                  type="date"
                  label="Tanggal Akhir"
                  placeholder="Masukan Tanggal Akhir"
                />
              </div>
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">
                  <i className="fa fa-print mr-2"></i>Print
                </button>
              </div>
            </div>
          </form>
        );
      default:
        break;
    }
  }
}

FormStok = reduxForm({
  form: "FormStok",
  enableReinitialize: true,
})(FormStok);
export default connect((state) => {
  return {
    listlokasi: state.provinsi.dataLokasiPindah,
    listkategori: state.provinsi.kategori,
    kodeBarang: state.provinsi.kodeBarang,
  };
})(FormStok);
