import jsPDF from "jspdf";
import "jspdf-autotable";
import { formatGram } from "../../helper";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanStockCard = (data) => {
  console.log(data);
  // initialize jsPDF
  const doc = new jsPDF("landscape");
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  doc.setFontSize(15);
  doc.text("LAPORAN STOCK - CARD ", 14, 15);
  doc.setFontSize(10);
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    let tableColumn = [
      [
        {
          content: `KODE LOKASI : ${item.kode_lokasi}`,
          colSpan: 6,
        },
        {
          content: `KODE JENIS : ${item.kode_jenis}`,
          colSpan: 5,
        },
      ],
      [
        {
          content: `TANGGAL`,
        },
        {
          content: `AWAL\nNETTO`,
        },
        {
          content: `AWAL\nBRUTO`,
        },
        {
          content: `IN\nNETTO`,
        },
        {
          content: `IN\nBRUTO`,
        },
        {
          content: `OUT\nNETTO`,
        },
        {
          content: `OUT\nBRUTO`,
        },
        {
          content: `AKHIR\nNETTO`,
        },
        {
          content: `AKHIR\nBRUTO`,
        },
        {
          content: `KETERANGAN`,
        },

        {
          content: `NO BON`,
        },
      ],
    ];
    item.detail.forEach((hasil) => {
      let rows = [
        hasil.tanggal,
        formatGram(hasil.awal_netto),
        formatGram(hasil.awal_bruto),
        formatGram(hasil.in_netto),
        formatGram(hasil.in_bruto),
        formatGram(hasil.out_netto),
        formatGram(hasil.out_bruto),
        formatGram(hasil.akhir_netto),
        formatGram(hasil.akhir_bruto),
        hasil.keterangan,
        hasil.no_ext,
      ];
      tableRows.push(rows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      startY: 30,
      theme: "plain",
      rowPageBreak: "avoid",
      margin: { top: 20 },
      bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
      headStyles: {
        lineWidth: 0.02,
        lineColor: "#000",
        fillColor: [212, 212, 211],
      },
      columnStyles: {
        10: { cellWidth: 40 },
        11: { cellWidth: 40 },
        // etc
      },
    });
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN STOCK - CARD",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN STOCK - CARD</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanStockCard;
