import React, { Component } from "react";
import { AxiosMasterGet } from "../Axios";
import { getToday } from "../GlobalComponent/function";
import { ToastError } from "../GlobalComponent/notification";
import { checkLaporan } from "../helper";
import FormStok from "./Component/FormStok";
import LaporanPrintMutasi from "./Component/LaporanPrintMutasi";
import LaporanStockCard from "./Component/LaporanStockCard";
import LaporanStockPerKategori from "./Component/LaporanStockPerKategori";
import LaporanStockPerKategoriLokasi from "./Component/LaporanStockPerKategoriLokasi";
import LaporanStockSummary from "./Component/LaporanStockSummary";

class LaporanStok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLaporan: [],
    };
  }
  handlePindah(data) {}
  handleMutasi(hasil) {
    AxiosMasterGet(
      `report/mutasi-stock/${hasil.kode_lokasi}&${hasil.tanggal_awal}&${hasil.tanggal_akhir}`
    )
      .then((res) => {
        if (checkLaporan(res.data)) {
          LaporanPrintMutasi(
            hasil.tanggal_awal,
            hasil.tanggal_akhir,
            "ADMIN",
            getToday(),
            "ADMIN",
            res.data
          );
        }
      })
      .catch(() => ToastError("Data Kosong"));
  }
  handleKirimMasak(data) {}
  handleTerimaMasak(data) {}
  handleStockPerKategori(hasil) {
    hasil.group_by === "KATEGORI"
      ? AxiosMasterGet(
          "report/stock-barang/" +
            `${hasil.group_by}&${hasil.filter_by}&${hasil.kode_kategori}`
        )
          .then((res) => {
            if (checkLaporan(res.data)) {
              this.setState({
                listLaporan: res && res.data,
              });
            }
          })
          .then(() => LaporanStockPerKategori(this.state.listLaporan))
      : AxiosMasterGet(
          "report/stock-barang/" +
            `${hasil.group_by}&${hasil.filter_by}&${hasil.kode_lokasi || "ALL"}`
        )
          .then((res) =>
            this.setState({
              listLaporan: res && res.data,
            })
          )
          .then(() => LaporanStockPerKategoriLokasi(this.state.listLaporan))
          .catch(() => ToastError("Data Kosong"));
  }
  handleStockCard(hasil) {
    AxiosMasterGet(
      `report/stock-barang-card/${hasil.kode_lokasi}&${hasil.kode_jenis}&${hasil.tanggal_awal}&${hasil.tanggal_akhir}`
    )
      .then((res) => {
        if (checkLaporan(res.data)) {
          LaporanStockCard(res.data);
        }
      })
      .catch(() => ToastError("Data Kosong"));
  }
  handleSummary(hasil) {
    AxiosMasterGet(
      `report/stock-barang-summary/${hasil.kode_lokasi}&${hasil.tanggal_awal}&${hasil.tanggal_akhir}`
    )
      .then((res) => {
        if (checkLaporan(res.data)) {
          LaporanStockSummary(res.data);
        }
      })
      .catch((err) => ToastError(err));
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                  <li className="nav-item ">
                    <a
                      href="#stok-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link active"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Stock Barang</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#kartu-stok-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">
                        Kartu Stok Barang
                      </span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#summary-stok-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Summary Barang</span>
                    </a>
                  </li>
                  <li className="nav-item">
                    <a
                      href="#mutasi-tab"
                      data-toggle="tab"
                      aria-expanded="false"
                      className="nav-link"
                    >
                      <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                      <span className="d-none d-lg-block">Mutasi Barang</span>
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div className="tab-pane show " id="saldo-tab">
                    <FormStok from="saldoAwal" />
                  </div>
                  <div className="tab-pane" id="pindah-tab">
                    <FormStok
                      from="perpindahanBarang"
                      onSubmit={(data) => this.handlePindah(data)}
                    />
                  </div>
                  <div className="tab-pane" id="mutasi-tab">
                    <FormStok
                      from="mutasi"
                      onSubmit={(data) => this.handleMutasi(data)}
                    />
                  </div>
                  <div className="tab-pane active" id="stok-tab">
                    <FormStok
                      from="stokPerKategori"
                      onSubmit={(data) => this.handleStockPerKategori(data)}
                    />
                  </div>
                  <div className="tab-pane" id="kartu-stok-tab">
                    <FormStok
                      from="kartuStok"
                      onSubmit={(data) => this.handleStockCard(data)}
                    />
                  </div>
                  <div className="tab-pane" id="sales-tab">
                    <FormStok from="soSales" />
                  </div>
                  <div className="tab-pane" id="jenis-lk-tab">
                    <FormStok from="jenisJK" />
                  </div>
                  <div className="tab-pane" id="jenis-rongsok-tab">
                    <FormStok from="jenisJK" />
                  </div>
                  <div className="tab-pane" id="terima-masak-tab">
                    <FormStok
                      from="terimaMasak"
                      onSubmit={(data) => this.handleTerimaMasak(data)}
                    />
                  </div>
                  <div className="tab-pane" id="kirim-masak-tab">
                    <FormStok
                      from="kirimMasak"
                      onSubmit={(data) => this.handleKirimMasak(data)}
                    />
                  </div>
                  <div className="tab-pane" id="summary-stok-tab">
                    <FormStok
                      from="SummaryStock"
                      onSubmit={(data) => this.handleSummary(data)}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LaporanStok;

