import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanStockSummary = (data) => {
  console.log(data);
  // initialize jsPDF
  const doc = new jsPDF();
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  doc.setFontSize(15);
  doc.text("LAPORAN STOK BARANG SUMMARY ", 14, 15);
  doc.setFontSize(10);
  //   //row 1
  //   doc.text(`Supplier : ${row1isi}`, 14, 25);
  //   //   row 2
  //   doc.text(`Tanggal	: ${row2isi}`, 120, 25);
  data.forEach((item, index) => {
    let tableColumn = [
      [
        {
          content: `KODE LOKASI : ${item.kode_lokasi}`,
          colSpan: 9,
        },
      ],
      [
        {
          content: `KODE JENIS`,
        },
        {
          content: `AKHIR BRUTO`,
        },
        {
          content: `AKHIR NETTO`,
        },
        {
          content: `AWAL BRUTO`,
        },
        {
          content: `AWAL NETTO`,
        },
        {
          content: `IN BRUTO`,
        },
        {
          content: `IN NETTO`,
        },
        {
          content: `OUT BRUTO`,
        },
        {
          content: `OUT NETTO`,
        },
      ],
    ];
    item.detail_barang.forEach((hasil) => {
      let rows = [
        hasil.kode_jenis,
        hasil.akhir_bruto,
        hasil.akhir_netto,
        hasil.awal_bruto,
        hasil.awal_netto,
        hasil.in_bruto,
        hasil.in_netto,
        hasil.out_bruto,
        hasil.out_netto,
      ];
      tableRows.push(rows);
    });
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      startY: 30,
      theme: "plain",
      rowPageBreak: "avoid",

      margin: { top: 20 },
      bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
      headStyles: {
        lineWidth: 0.02,
        lineColor: "#000",
        fillColor: [212, 212, 211],
      },
    });
  });

  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN STOCK - SUMMARY",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN STOCK - SUMMARY</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanStockSummary;
